.caroussel {
    width: 100%;
    height: 415px;
    display: flex;
    position: relative;
    border-radius: 25px;

    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.carousselNavigation {
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: space-between;

    img {
        height: 47px;
        -moz-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }
}

.prevBoutton,
.nextBoutton {
    display: flex;
    align-items: center;
    height: 100%;
    cursor: pointer;

    img {
        z-index: 1;
    }
}

.prevBoutton {

    img {
        transform: rotate(270deg)
    }

    &:hover {
        &::after {
            opacity: 0.2;
            transition: opacity 300ms ease;
        }

    }

    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 75.2px;
        height: 100%;
        background:
            rgba(0, 0, 0);
        opacity: 0;
        border-radius: 25px 0px 0px 25px;
        transition: opacity 300ms ease;
    }

}

.nextBoutton {

    img {
        transform: rotate(90deg)
    }

    &:hover {
        &::after {
            opacity: 0.2;
            transition: opacity 300ms ease;
        }

    }

    &::after {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        width: 75.2px;
        height: 100%;
        background: rgba(0, 0, 0);
        opacity: 0;
        border-radius: 0px 25px 25px 0px;
        transition: opacity 300ms ease;
    }
}

.slidePosition {
    position: absolute;
    left: 48.95%;
    right: 49.11%;
    top: 86.19%;
    bottom: 6.01%;
    color: white;

    font-weight: 500;
    font-size: 18px;
    line-height: 142.6%;
    display: flex;
    align-items: flex-end;
    text-align: center;
}

@media (max-width: 768px) {
    .caroussel {
        height: 255px;
        border-radius: 10px;
    }

    .carousselNavigation img {
        height: 20px;
    }

    .prevBoutton,
    .nextBoutton {
        &::after {
            display: none;
        }
    }

    .slidePosition {
        display: none;
    }
}